@import url('https://fonts.googleapis.com/css?family=Lobster&display=swap');

body {
  background-color: aliceblue;
  background: url("./img/SMRPG.png");
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  perspective: 1000px;
  perspective-origin: 50% 50%;
  animation: animation 100s linear infinite;
}

.title {
  font-size: 5.6rem;
  font-family: 'Lobster', cursive;
}

.wrapper {
  background: url("./img/SMRPG.png");
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  perspective: 1000px;
  perspective-origin: 50% 50%;
  animation: animation 100s linear infinite;
}

@keyframes animation {
   100%{
    background-position:0px -3000px;
  }
}

.sizer{
  padding: 3em;
  height: 6em;
}

.panel {

    background: url(panel.png) no-repeat center center fixed; 
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;

}

.scroller{
  overflow: auto;
}

.flexor{
  display: flex;
    flex-wrap: wrap;
}

.relativer{
  position: relative;
  width: 16em;
  max-width: 16em !important;
  text-align: -webkit-center;
  text-align: center;
  margin-left: 5px;
  margin: 5px;
  margin-bottom: 1em;
}

.button-absoluter{
  position: absolute !important;
  bottom: 0 !important;
  right: 0;
  left: 0;
}

.bottomer{
  margin-bottom: 2em;
}

.flexer{
  display: block;
}

.sizor-item{
  max-width: 14em;
}

.diplay-stuff{
  width: 16em;
  max-width: 16em !important;
  text-align: -webkit-center;
  text-align: center;
}

.checkout-panels{
  margin-right: 1em;
}

.sizor-checkout{
  max-width: 3em;
}

.topper{
  margin: auto;
  max-width: 90vw;
    min-width: 90vw;
}

.strecher{
  width: 100%;
}

#strecher{
  width: 100%;
}